<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent>
    <template v-slot:activator="{ on }">
      <v-btn 
        depressed
        v-on="on"
        class="mb-6"
        color="primary">
        <v-icon class="mr-3">mdi-plus-circle</v-icon>
        Tambah data
      </v-btn>
    </template>

    <v-card>
      <v-card-title class="py-2">
        Tambah Sekolah
      </v-card-title>
      <v-divider></v-divider>
      <v-card-text class="pa-6 pb-0">
        <v-form v-model="valid" ref="form">          
            <v-autocomplete
              label="Provinsi"
              outlined
              dense
              v-model="dataPeringkat.provinsi"
              :items="data"
              required
              :rules="requiredRule"
            ></v-autocomplete>                
            <v-autocomplete
              label="Kabupaten"
              outlined
              dense
              v-model="dataPeringkat.kabupaten"
              :items="data"
              required
              :rules="requiredRule"
            ></v-autocomplete>                
            <v-autocomplete
              label="Jenjang"
              outlined
              dense
              v-model="dataPeringkat.jenjang"
              :items="data"
              required
              :rules="requiredRule"
            ></v-autocomplete>                
            <v-autocomplete
              label="Sekolah"
              outlined
              dense
              v-model="dataPeringkat.sekolah"
              :items="data"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
        </v-form>
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          text
          @click="dialog = false">
          kembali
        </v-btn>
        <v-btn
          :disabled="!valid"
          color="primary"
          @click.prevent="validate"
          type="submit"
          depressed>
          simpan
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    data () {
      return {
        dialog: false,
        valid: true,
        dataPeringkat: {
          provinsi: '',
          kabupaten: '',
          jenjang: '',
          sekolah: ''
        },
        requiredRule: [v => !!v || 'Data harus diisi']
      }
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          console.log(this.dataPeringkat)
        }
      }
    }
  }
</script>
