<template>
	<v-data-table
		:headers="header"
		:items="data"
		:search="search"
		:items-per-page="10">		
		<template v-slot:top>
			<v-text-field
        v-model="search"
        prepend-icon="mdi-magnify"
        label="Cari peserta"
        single-line
        autocomplete="off"
        hide-details
      ></v-text-field>
		</template>
		<template v-slot:no-data>
      <v-alert type="info" class="mt-4">
        <div>Data tidak ditemukan</div>
      </v-alert>
    </template>
		<template v-slot:item.action="{item}">
			<v-btn 
				icon color="success">
				<v-icon small>mdi-eye</v-icon>
			</v-btn>
			<v-btn icon color="primary">
				<v-icon small>mdi-pencil</v-icon>
			</v-btn>
			<v-btn icon color="red">
				<v-icon small>mdi-trash-can</v-icon>
			</v-btn>
		</template>		
	</v-data-table>
</template>

<script>
	export default{
		data: () => ({
			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'name', sortable: false },
				{ text: 'Akreditasi', value: 'prodi', sortable: false },
				{ text: 'Jenjang', value: 'asal', sortable: false },
				{ text: 'IPA', value: 'user', sortable: false },
				{ text: 'IPS', value: 'pass', sortable: false },
				{ text: 'Bahasa', value: 'aktivasi', sortable: false },
				{ text: 'Keagamaan', value: 'aktivasi', sortable: false },
				{ text: 'Total', value: 'aktivasi', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false }
			],

			data: [
				{
					name: 's',
					asal: 's',
					prodi: 's',
					user: 's',
					pass: 's',
					aktivasi: 's'
				}
			],

			search: ''
		})
	}
</script>