<template>
  <v-card class="ma-md-6 ma-sm-3">
    <v-card-title class="py-2">
      <v-icon class="mr-3">mdi-desktop-mac</v-icon>
      Daftar Pemeringkatan Sekolah
    </v-card-title>
    <v-divider></v-divider>
    <v-card-text>
      <TambahPeringkat/>
      <v-form ref="form" v-model="valid">
        <v-row dense>
          <v-col cols="12" md="3" sm="6" class="py-0">
            <v-autocomplete
              label="Provinsi"
              outlined
              dense
              v-model="dataFilter.provinsi"
              :items="data"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="3" sm="6" class="py-0">
            <v-autocomplete
              label="Kabupaten"
              outlined
              dense
              v-model="dataFilter.kabupaten"
              :items="data"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="3" sm="6" class="py-0">
            <v-autocomplete
              label="Jenjang"
              outlined
              dense
              v-model="dataFilter.jenjang"
              :items="data"
              required
              :rules="requiredRule"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="3" sm="6" class="py-0">
            <v-btn
              type="submit"
              :disabled="!valid"
              @click.prevent="validate"
              color="primary"
              depressed>
              filter
            </v-btn>
          </v-col>
        </v-row>
      </v-form>

      <DataPeringkat/>
    </v-card-text>
  </v-card>
</template>

<script>
  import DataPeringkat from '@/components/Peringkat/DataPeringkat'
  import TambahPeringkat from '@/components/Peringkat/TambahPeringkat'

  export default{
    data: () => ({
      valid: true,
      requiredRule: [v => !!v || 'Data harus diisi'],
      data: ['foo'],
      dataFilter: {
        provinsi: '',
        kabupaten: '',
        jenjang: ''
      }
    }),

    components: {
      TambahPeringkat,
      DataPeringkat
    },

    methods: {
      validate () {
        if (this.$refs.form.validate()) {
          console.log(this.dataFilter)
        }
      }
    }
  }
</script>
